a {
  color: inherit;
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

button {
  cursor: pointer;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --accent-color: #935116;
  --section-color: #fbeee6;
  --white: #fff;
  --text-color: #000;
  --btn-bg: #f1ebe7;
  --detail-btn-hover-color: #e98242;
  --input-border: rgba(150, 93, 6, .2);
  --backdrop-color: rgba(147, 121, 113, .81);
}

@font-face {
  font-family: Cormorant;
  src: url("Cormorant-Italic.a03304e0.ttf");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: Inter;
  src: url("Inter-Regular.74535f8a.ttf");
  font-style: normal;
  font-weight: 400;
}

body {
  color: var(--text-color);
  font-family: Inter, sans-serif;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
