.btn {
  width: 90px;
  background: var(--btn-bg);
  border: 1px solid var(--accent-color);
  color: var(--accent-color);
  z-index: 1;
  border-radius: 20px;
  margin-left: 5px;
  padding: 5px;
  font-size: 3vw;
  font-weight: 700;
  line-height: 1.17;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  left: -36px;
  transform: scale(1);
}

@media screen and (min-width: 480px) {
  .btn {
    width: 143px;
    font-size: 17px;
    line-height: 1.17;
  }
}

@media screen and (min-width: 768px) {
  .btn {
    width: 150px;
    left: 55px;
  }
}

@media screen and (min-width: 1200px) {
  .btn {
    width: 250px;
    padding: 10px;
    left: 90px;
  }
}

.btn:hover, .btn:focus {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.1);
}

.detail-btn {
  z-index: 1;
  border: 1px solid var(--white);
  color: var(--white);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  padding: 5px 7px;
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translate(-50%);
}

.detail-btn:hover, .detail-btn:focus {
  background-color: var(--white);
  color: var(--detail-btn-hover-color);
}

@media screen and (min-width: 1200px) {
